@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  font-display: swap;
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  height: 100%;
  font-family: "Roboto", "Arial", sans-serif;
  color: #252525;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  margin: 0;
  min-height: 100%;
  height: 100%;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  cursor: pointer;
  text-decoration: none;
  color: inherit;
  outline: 0;
}

ul {
  list-style: none;
}

button {
  cursor: pointer;
  border: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.fw-500 {
  font-weight: 500;
}

.fw-600 {
  font-weight: 600;
}

.hint-text {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1rem;
  color: rgba(31, 32, 32, 0.3);
}

@media (min-width: 1020px) {
  .desktop-hidden {
    display: none;
  }
}
.container {
  box-sizing: border-box;
  max-width: 1230px;
  padding: 0 24px;
  margin: 0 auto;
  position: relative;
}

@media (min-width: 667px) {
  .container {
    padding: 0 24px;
  }
}
@media (min-width: 767px) {
  .container {
    padding: 0 50px;
    min-width: 320px;
  }
}
@media (min-width: 1020px) {
  .container {
    padding: 0 30px;
  }
}
.swiper {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  z-index: 1;
}

.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

.swiper-container-pointer-events {
  touch-action: pan-y;
}

.swiper-container-pointer-events.swiper-container-vertical {
  touch-action: pan-x;
}

.swiper-slide {
  flex-shrink: 0;
  max-width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}

.swiper-slide-invisible-blank {
  visibility: hidden;
}

/* Auto Height */
.swiper-container-autoheight,
.swiper-container-autoheight .swiper-slide {
  height: auto;
}

.swiper-container-autoheight .swiper-wrapper {
  align-items: flex-start;
  transition-property: transform, height;
}

/* CSS Mode */
.swiper-container-css-mode > .swiper-wrapper {
  overflow: auto;
  scrollbar-width: none;
  /* For Firefox */
  -ms-overflow-style: none;
  /* For Internet Explorer and Edge */
}

.swiper-container-css-mode > .swiper-wrapper::-webkit-scrollbar {
  display: none;
}

.swiper-container-css-mode > .swiper-wrapper > .swiper-slide {
  scroll-snap-align: start start;
}

.swiper-container-horizontal.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: x mandatory;
}

.swiper-container-vertical.swiper-container-css-mode > .swiper-wrapper {
  scroll-snap-type: y mandatory;
}

.wrapper {
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  overflow-x: hidden;
}

.wrapper.modal-open::after {
  position: fixed;
  z-index: 30;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  content: "";
  background-color: rgba(0, 0, 0, 0.7);
}

.main {
  position: relative;
  flex: 1 0 auto;
}

.main-btn {
  background-color: #7bb899;
  color: #ffffff;
  border-radius: 20px;
  padding: 5px 8px;
}

.main-btn:hover {
  background-color: #389b5f;
}

.main-icon-link {
  display: inline-block;
  vertical-align: middle;
  gap: 9px;
  align-items: center;
  padding-left: 27px;
  position: relative;
}

.main-icon-link span {
  text-decoration: underline;
}

.main-icon-link__icon-wrap {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 19px;
  height: 19px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer {
  background-color: #252525;
  color: #ffffff;
  min-height: 150px;
  padding: 1rem 1.5rem;
}

@media (min-width: 767px) {
  .footer {
    min-height: 312px;
  }
}
.menu-button {
  display: none;
}

@media (max-width: 768px) {
  .menu-button {
    position: absolute;
    top: 0;
    right: 0;
    flex: 0 0 44px;
    width: 44px;
    height: 44px;
    display: grid;
    place-items: center;
    border: 0;
    outline: 0;
    background: transparent;
  }
}
.menu-button__icon {
  width: 22.5px;
  height: 1px;
  display: block;
  background: #000000;
  position: relative;
}

.menu-button__icon::after, .menu-button__icon::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 1px;
  background: #000000;
  left: 0;
  transition: transform 0.3s ease 0s;
}

.menu-button__icon::after {
  transform-origin: left bottom;
  bottom: 6px;
}

.menu-button__icon::before {
  transform-origin: left top;
  top: 6px;
}

.menu-button[aria-expanded=true] .menu-button__icon {
  height: 0;
  width: 30px;
}

.menu-button[aria-expanded=true] .menu-button__icon::after, .menu-button[aria-expanded=true] .menu-button__icon::before {
  background: #ffffff;
}

.menu-button[aria-expanded=true] .menu-button__icon::after {
  transform: translate(2px, -4px) rotate(45deg);
}

.menu-button[aria-expanded=true] .menu-button__icon::before {
  transform: translate(2px, 4px) rotate(-45deg);
}

.nav-actions {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: nowrap;
  margin-left: -14px;
  margin-right: -14px;
}

.nav-actions__link {
  display: inline-block;
  vertical-align: middle;
  padding: 8px 14px;
  position: relative;
}

.nav-actions__link:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 1px;
  height: 10px;
  background-color: #000000;
  right: 0;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .nav-actions__link:hover {
    text-decoration: underline;
  }
}
.header-search-nav {
  display: grid;
  grid-template-columns: min-content 1fr auto;
  align-items: stretch;
  justify-content: space-between;
  background-color: transparent;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  overflow: hidden;
  font-weight: 600;
  font-size: 0.6875rem;
  line-height: 0.8125rem;
  color: #ffffff;
}

@media (min-width: 667px) {
  .header-search-nav {
    font-size: 0.875rem;
    line-height: 1rem;
  }
}
.header-search-nav__icon-search {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 8px;
  background-color: #ffcc00;
  border-radius: 10px 0 0 10px;
}

.header-search-nav__list {
  padding-left: 1.25rem;
  background: #252525;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: nowrap;
  scroll-snap-type: x mandatory;
  overscroll-behavior-x: contain;
  max-width: 100%;
  scroll-padding-left: 1rem;
  overflow: hidden;
  overflow-x: auto;
}

@media (min-width: 767px) {
  .header-search-nav__list {
    padding-left: 3.6875rem;
    overflow: initial;
    flex-wrap: wrap;
  }
}
.header-search-nav__list-item {
  scroll-snap-align: start;
}

.header-search-nav__link {
  display: inline-block;
  vertical-align: middle;
  padding: 4px 9px;
}

.header-search-nav__link.is-active {
  color: #252525;
  background-color: #ffcc00;
}

@media (min-width: 667px) {
  .header-search-nav__link {
    padding: 9px 18px;
  }
}
.header-search-nav__special-link {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 15px 5px 14px;
  text-align: center;
  background-color: #ffcc00;
  font-weight: 600;
  font-size: 0.6875rem;
  line-height: 0.8125rem;
  color: #000000;
  text-transform: uppercase;
}

@media (min-width: 667px) {
  .header-search-nav__special-link {
    font-size: 0.875rem;
    line-height: 1rem;
  }
}
.header {
  padding: 1rem 0;
}

@media (min-width: 767px) {
  .header {
    padding: 8px 0 16px;
  }
}
.header__top {
  margin-bottom: 26px;
  position: relative;
  display: grid;
  grid-template-columns: 95px 1fr 44px;
  grid-template-rows: minmax(94px, auto) 1fr;
  grid-template-areas: "logo . ." ". . .";
  column-gap: 0.8125rem;
}

@media (min-width: 767px) {
  .header__top {
    display: grid;
    align-items: start;
    row-gap: 1.375rem;
    column-gap: 1.875rem;
    grid-template-columns: min-content 1fr;
    grid-template-rows: auto 1fr;
    grid-template-areas: "logo nav" " info info";
  }
}
@media (min-width: 1020px) {
  .header__top {
    grid-template-columns: 210px 1fr;
    grid-template-rows: auto 1fr;
    grid-template-areas: "logo nav" "logo info";
  }
}
.header__logo-wrap {
  grid-area: logo;
  align-self: start;
  background-color: #ffcc00;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@media (min-width: 767px) {
  .header__logo-wrap {
    margin-top: 11px;
    min-height: 134px;
  }
}
.header__logo-wrap svg {
  width: 40px;
  height: 40px;
  margin: 10px auto 6px;
}

@media (min-width: 767px) {
  .header__logo-wrap svg {
    width: 95px;
    height: 95px;
  }
}
.header__logo-wrap p {
  display: block;
  width: 100%;
  background-color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  padding-top: 8px;
  font-weight: 700;
  font-size: 0.6875rem;
  line-height: 0.9375rem;
  letter-spacing: -0.1px;
}

@media (min-width: 767px) {
  .header__nav {
    border-bottom: 1px solid #d7d7d7;
  }
}
.nav {
  font-size: 0.75rem;
  line-height: 0.875rem;
  letter-spacing: -0.01em;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}

.nav__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: -0.75rem;
  margin-right: -0.75rem;
}

.nav__item-link {
  font-size: inherit;
  line-height: inherit;
  padding: 0.5rem 0.75rem;
  display: inline-block;
  vertical-align: middle;
}

@media (hover: hover), screen and (min-width: 0\0 ) {
  .nav__item-link:hover {
    text-decoration: underline;
  }
}
.header__main-info {
  grid-area: info;
}

@media (max-width: 767px) {
  .header__main-info {
    grid-column: 1/-1;
    grid-row: 1/3;
  }
}
.header-main-info {
  display: grid;
  gap: 0.8125rem;
  grid-template-columns: 95px 1fr 1fr 44px;
  grid-template-rows: minmax(94px, auto) 1fr;
  grid-template-areas: "... cart cart ..." "banner banner banner banner" "time time time time";
}

@media (min-width: 1020px) {
  .header-main-info {
    grid-template-areas: "... cart cart ..." "banner banner time time";
  }
}
@media (min-width: 1020px) {
  .header-main-info {
    grid-template-columns: 95px auto 1fr 44px;
    grid-template-areas: "... cart time ..." "banner banner banner banner";
  }
}
@media (min-width: 767px) {
  .header-main-info {
    display: flex;
    flex-wrap: wrap;
    gap: 2.0625rem;
    justify-content: flex-start;
  }
}
.header-main-info__banner {
  grid-area: banner;
  flex: 1 0 253px;
  display: flex;
  max-width: 100%;
  background-color: #d7d7d7;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: auto;
  aspect-ratio: 253/70;
}

@media (min-width: 767px) {
  .header-main-info__banner {
    flex: 0 0 253px;
    width: 253px;
    height: 70px;
  }
}
.header-main-info__banner img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.header-main-info__header-cart {
  grid-area: cart;
}

@media (min-width: 767px) {
  .header-cart {
    display: grid;
    align-items: start;
    column-gap: 35px;
    row-gap: 15px;
    grid-template-columns: auto 240px;
  }
}
.header-cart__data-links {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
}

@media (min-width: 667px) {
  .header-cart__data-links {
    display: grid;
    align-items: center;
    justify-content: start;
  }
}
.header-cart__cart-link {
  font-size: 0.75rem;
  line-height: 0.875rem;
  letter-spacing: -0.01em;
}

.header-cart__cart-link--cart {
  font-size: 0.875rem;
  line-height: 1rem;
}

.header-cart__product-list {
  display: none;
}

@media (min-width: 767px) {
  .header-cart__product-list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    scroll-snap-type: x mandatory;
    overscroll-behavior-x: contain;
    max-width: 100%;
    overflow: hidden;
  }

  .header-cart__product-list li {
    scroll-snap-align: start;
    flex: 0 0 70px;
  }

  .header-cart__product-list li:not(:last-child) {
    margin-right: 15px;
  }

  .header-cart__product-list a {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.header-main-info__header-additional-info {
  grid-area: time;
}

@media (min-width: 1020px) {
  .header-main-info__header-additional-info {
    margin-left: auto;
  }
}
.header-additional-info__time {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.75rem;
  line-height: 0.875rem;
  letter-spacing: -0.01em;
  margin-bottom: 0.9375rem;
}

@media (min-width: 767px) {
  .header-additional-info__time {
    margin-bottom: 1rem;
    margin-top: -4px;
  }
}
.header-additional-info__time-value {
  letter-spacing: -0.03em;
}

.header-additional-info__tel-link {
  display: inline-block;
  vertical-align: middle;
  font-weight: 700;
  font-size: 1.5625rem;
  line-height: 1.8125rem;
  letter-spacing: -0.02em;
  white-space: nowrap;
  padding-bottom: 0.8125rem;
  border-bottom: 1px solid #d7d7d7;
}

.header-region {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.75rem;
  line-height: 0.875rem;
  letter-spacing: -0.01em;
  padding: 4px 0;
}

.header-region__change-link {
  display: flex;
  align-items: center;
}

.header-region__change-link span {
  position: relative;
  border-bottom: 1px dashed #000000;
}

.header-region__change-link::before {
  content: "";
  flex: 0 0 7px;
  display: block;
  margin-right: 4px;
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 3.25px 0 3.25px 7px;
  border-color: transparent transparent transparent #000000;
}

.header-menu--opened .header__menu-button {
  z-index: 100;
}

@media (max-width: 767px) {
  .header-menu--opened .header__nav {
    transition: opacity 0.3s ease-out 0s, visibility 0.3s ease-out 0s;
    transform: translateY(0);
    z-index: 50;
    opacity: 1;
    visibility: visible;
    padding: 50px 0;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    overflow-y: auto;
    background: rgba(0, 0, 0, 0.7);
    backdrop-filter: blur(4px);
    flex-direction: column;
    justify-content: stretch;
    flex-wrap: nowrap;
    align-items: center;
    font-size: 2rem;
    line-height: 1;
    color: #ffffff;
    text-align: center;
  }
}
.header-menu--opened .nav__wrapper {
  flex-direction: column;
  align-items: center;
}

@media (max-width: 768px) {
  .header-menu--closed .header__nav {
    position: absolute;
    opacity: 0;
    visibility: hidden;
  }
}